import { clientApi } from '../../helpers/newClientApi'

const state = {
  category: {},
  categories: [],
  isMoreCategoriesAvialiable: true,
  pagination: {
    limit: 50,
    offset: 0,
    count: 0
  },
  foundedCategories: []
}

const getters = {
  getCategories: () => state.categories,
  isMoreCategoriesAvialiable: () => state.isMoreCategoriesAvialiable,
  foundedCategories: () => state.foundedCategories,
  getCategory: () => state.category
}

const mutations = {
  setCategory(sub, category) {
    state.category = category
  },
  setCategories(sub, { categories, count }) {
    if (state.pagination.offset > 0) {
      state.categories = [
        ...state.categories,
        ...categories
      ]
    } else {
      state.categories = categories
    }
    state.pagination.count = count
  },
  updateCategory(sub, { category, id }) {
    const index = state.categories.indexOf(item => item.category_id === id)
    state.categories[index] = {
      ...category,
      ...state.category
    }
  },
  updateCategoryForm(sub, payload) {
    state.category[payload.key] = payload.value
  },
  setCategoriesPagination(sub, { limit, newOffset }) {
    state.pagination = {
      limit,
      offset: newOffset
    }
  },
  stopCategoryLazzyLoad() {
    state.isMoreCategoriesAvialiable = !state.isMoreLocationAvialiable
  },
  removeCategory(sub, id) {
    state.categories = state.categories.filter(item => item.category_id !== id)
  },
  setFoundedCategories(sub, data) {
    state.foundedCategories = data
  }
}

const actions = {
  createCategory({ commit, rootState }) {
    const { category } = rootState.categories
    clientApi('post', 'categories', category)
      .then(() => {
        commit('setCategory', category)
        const payload = {
          type: 'success',
          msg: 'Category created',
          auto: true
        }
        commit('SET_ALERT', payload)
        commit('DISPLAY_ALERT')
        if (payload.auto) {
          setTimeout(() => {
            commit('DISPLAY_ALERT')
          }, 2000)
        }
      })
      .catch(console.log)
  },
  fetchCategory({ commit }, { id }) {
    clientApi('get', `categories/${id}`)
      .then(({ data: { category } }) =>
        commit('setCategory', category)
      )
  },
  updateCategory({ commit, dispatch }, { id }) {
    const { type, title, title_cn, poster, description } = state.category
    const category = {
      type,
      title,
      title_cn,
      poster,
      description
    }
    clientApi(
      'put',
      `categories/${id}`,
      { ...category }
    )
      .then(() => {
        commit('updateCategory', { category, id })
        const payload = {
          type: 'success',
          msg: 'Category updated',
          auto: true
        }
        commit('SET_ALERT', payload)
        commit('DISPLAY_ALERT')
        setTimeout(() =>
          commit('DISPLAY_ALERT')
        , 2000)
      })
      .catch(console.log)
  },
  removeCategory({ commit }, { type, id }) {
    clientApi('delete', `categories/${id}`)
      .then(() => {
        commit('removeCategory', id)
        const payload = {
          type: 'success',
          msg: ' ',
          auto: true
        }
        commit('SET_ALERT', payload)
        commit('DISPLAY_ALERT')
        if (payload.auto) {
          setTimeout(() => {
            commit('DISPLAY_ALERT')
          }, 2000)
        }
      })
      .catch(console.log)
  },
  showMoreCategories({ commit, rootState, dispatch }) {
    const { pagination: { offset, limit } } = rootState.categories
    const newOffset = offset + limit
    commit('setCategoriesPagination', { limit, newOffset })
    dispatch('fetchCategories')
  },
  fetchCategories({ commit, rootState }, type) {
    const { pagination: { offset, limit } } = rootState.categories
    clientApi('get', `categories?limit=${limit}&offset=${offset}`)
      .then(
        ({
          data: {
            categories: { rows, count }
          }
        }) => {
          if (rows.length < count) {
            commit('stopLazzyLoad')
          }
          commit('setCategories', { categories: rows, count })
        })
      .catch(console.log)
  },
  findCategory({ commit }, s) {
    clientApi('get', `categories/search?s=${s}`)
      .then(({ data: { category } }) =>
        commit('setFoundedCategories', category))
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



