import { clientApi, request } from '../../helpers/newClientApi'

const weChatApiUrl = 'https://api.weixin.qq.com/wxa/getwxacodeunlimit'
const urls = {
  location: 'pages/locations/show/show?location_id=',
  event: 'pages/events/show/show?id='
}

const params = {
  scene: 245,
  width: 430,
  auto_color: false,
  is_hyaline: true,
  line_color: {
    'r': 0,
    'g': 0,
    'b': 0
  }
}

const state = {
  type: 'location'
}

const actions = {
  async generateQr({ commit }, { type, id }) {
    console.log(type, id, 'important')
    const { data: { access_token } } = await clientApi('get', 'qr')
    const qr = await request(
      'post',
      `${weChatApiUrl}?access_token=${access_token}`, // eslint-disable-line
      {
        ...params,
        path: `${urls[type]}${id}`
      }
    )
    console.log(access_token, qr, 'important')
    await commit('finishGenaration')
  }
}

const mutations = {
  finishGenaration() {
    state.finished = true
  }
}

const getters = {}

export default {
  state,
  actions,
  getters,
  mutations
}



