
import { stringify } from 'query-string'
import { uniq } from 'ramda'
import { prepareEvent, prepareList } from '../handlers/eventHandler'
import { clientApi } from '../../helpers/newClientApi'

const state = {
  event: {
    poster: '',
    title: '',
    linkedId: 'none',
    location: ''
  },
  events: [],
  pagination: {
    limit: 10,
    offset: 0,
    count: 0
  },
  isMoreAvialiable: true,
  searchValue: ''
}

const getters = {
  getEvents: () => uniq(state.events),
  getEvent: () => state.event,
  isMoreAvialiable: () => state.isMoreAvialiable
}

const mutations = {
  setEvent(sub, event) {
    state.event = {
      ...prepareEvent(event)
    }
  },
  setPoster(sub, poster) {
    state.event.poster = poster
  },
  updateEventForm(sub, payload) {
    state.event[payload.key] = payload.value
  },
  setEvents(sub, { events, count }) {
    if (state.pagination.offset > 0) {
      state.events = [
        ...state.events,
        ...events
      ]
    } else {
      state.events = events
    }
    state.pagination.count = count
  },
  resetEvent() {
    for (let key in state.event) {
      state.event[key] = ''
    }
    state.event.poster = 'none'
  },
  addEvent(sub, event) {
    state.events.push(event)
  },
  updateEvent(sub, event, id) {
    const index = state.events.indexOf(({ event_id }) => event_id === id) // eslint-disable-line
    state.events[index] = { ...event }
  },
  removeEvent(sub, id) {
    state.events = state.events.filter(
      ({ event_id }) => event_id !== id // eslint-disable-line
    )
  },
  setPagination(sub, { limit, newOffset }) {
    state.pagination = {
      limit,
      offset: newOffset
    }
  },
  stopLazzyLoad() {
    state.isMoreAvialiable = !state.isMoreAvialiable
  },
  saveEventSearchValue(sub, value) {
    state.searchValue = value
  }
}

const actions = {
  async createEvent({ commit, rootState, dispatch }) {
    const { event, event: { location_id, category_id, days, startDate, endDate } } = state
    const newStartDate = startDate instanceof Date ? Date.parse(startDate) : startDate
    const newEndDate = endDate instanceof Date ? Date.parse(endDate) : endDate
    const updatedEvent = {
      ...event,
      category_id: category_id.category_id,
      location_id: location_id.location_id,
      location_name: location_id.title,
      place_id: location_id.location_id,
      days: stringify(days, { arrayFormat: 'bracket' }),
      startDate: newStartDate,
      endDate: newEndDate
    }
    try {
      await clientApi('post', 'events', updatedEvent)
      await commit('addEvent', updatedEvent)
      await dispatch('showAlert', 'Event created', { root: true })
    } catch (err) {
      throw err
    }
  },
  async updateEvent({ commit, rootState, dispatch }, { id }) {
    const { event, event: { location_id, category_id, days, startDate, endDate } } = rootState.events
    const newStartDate = startDate instanceof Date ? Date.parse(startDate) : startDate
    const newEndDate = endDate instanceof Date ? Date.parse(endDate) : endDate
    const updatedEvent = {
      ...event,
      category_id: category_id.category_id,
      place_id: location_id.location_id,
      location_name: location_id.title,
      days: stringify(days, { arrayFormat: 'bracket' }),
      startDate: newStartDate,
      endDate: newEndDate
    }
    try {
      await clientApi('put', `events/${id}`, { ...updatedEvent })
      await dispatch('showAlert', 'Event updated succesfull', { root: true })
      await commit('updateEvent', event, id)
    } catch (err) {
      throw err
    }
  },
  async removeEvent({ dispatch, commit }, { id }) {
    try {
      await clientApi('delete', `events/${id}`)
      await commit('removeEvent', id)
      await dispatch('showAlert', 'Event deleted succesfull', { root: true })
    } catch (err) {
      throw err
    }
  },
  async fetchEvent({ commit }, { id }) {
    try {
      const { data } = await clientApi('get', `events/${id}`)
      commit('setEvent', data)
    } catch (err) {
      throw err
    }
  },
  showMoreEvents({ commit, rootState, dispatch }) {
    const { pagination: { offset, limit } } = rootState.events
    const newOffset = offset + limit
    commit('setPagination', { limit, newOffset })
    dispatch('fetchEvents')
  },
  async fetchEvents({ commit, rootState }) {
    const { pagination: { offset, limit } } = rootState.events
    try {
      const { data: { count, rows } } = await clientApi(
        'get',
        `events?limit=${limit}&offset=${offset}`
      )
      if (rows.length < limit) {
        commit('stopLazzyLoad')
      }
      const events = prepareList(rows)
      await commit(
        'setEvents',
        {
          events,
          count
        }
      )
    } catch (err) {
      throw err
    }
  },
  async globalEventSearch({ commit, dispatch }, s) {
    commit('saveEventSearchValue', s)
    if (s.length === 0) {
      return dispatch('fetchEvents')
    }
    try {
      const { data } = await clientApi('get', `events/globalSearch?s=${s}`)
      const events = prepareList(data)
      await commit(
        'setEvents',
        {
          events,
          count: 0
        }
      )
    } catch (err) {
      throw err
    }
  },
  async orderEvents({ dispatch }, items) {
    items.map(async({ event_id, order }) => {
      await clientApi(
        'put',
        `events/${event_id}`,
        { order }
      )
    })
    await dispatch('showAlert', 'Events was reordered', { root: true })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



