const fgInput = () => import('ui/Inputs/formGroupInput.vue')
const fgSelect = () => import('ui/Inputs/formGroupSelect.vue')
const cascader = () => import('ui/Inputs/formInputSelect.vue')
const checkbox = () => import('ui/Inputs/formGroupCheckbox.vue')
const fgTextarea = () => import('ui/Inputs/formGroupTextarea.vue')
const DropDown = () => import('ui/Dropdown.vue')
const Alert = () => import('ui/Alerts/Alert.vue')
const Jumbotron = () => import('ui/Jumbotron/Jumbotron.vue')
const SelectSearch = () => import('ui/Inputs/selectSearch.vue')
const SearchBox = () => import('ui/SearchBox/SearchBox.vue')
const Card = () => import('ui/Cards/Card.vue')

const GlobalComponents = {
  install(Vue) {
    Vue.component('fg-input', fgInput)
    Vue.component('fg-select', fgSelect)
    Vue.component('fg-checkbox', checkbox)
    Vue.component('cascader', cascader)
    Vue.component('fg-textarea', fgTextarea)
    Vue.component('drop-down', DropDown)
    Vue.component('alert', Alert)
    Vue.component('jumbotron', Jumbotron)
    Vue.component('SelectSearch', SelectSearch)
    Vue.component('SearchBox', SearchBox)
    Vue.component('Card', Card)
  }
}

export default GlobalComponents



