import { clientApi } from '../../helpers/newClientApi'
import moment from 'moment'

const state = {
  oa: {},
  oas: [],
  isMoreOasAvialiable: true,
  searchType: '',
  pagination: {
    limit: 50,
    offset: 0,
    count: 0
  },
  setFoundedItems: []
}

const getters = {
  isMoreOasAvialiable: () => state.isMoreOasAvialiable,
  getOas: () => state.oas,
  getOa: () => state.oa
}

const mutations = {
  setNewOa(sub, oa) {
    state.oas = [
      ...state.oas,
      oa
    ]
  },
  clearOaSearch() {
    state.setFoundedItems = []
  },
  setOa(sub, oa) {
    state.oa = oa
    console.log(oa, 'IMPORTANT')
    state.oa.initialValue = oa[oa.type].title
  },
  setFoundedOas(sub, { oas }) {
    state.oas = oas
  },
  setOas(sub, { oas, count }) {
    if (state.pagination.offset > 0) {
      state.oas = [
        ...state.oas,
        ...oas
      ]
    } else {
      state.oas = oas
    }
    state.pagination.count = count
  },
  updateOa(sub, { oa, id }) {
    const index = state.oas.indexOf(({ oa_id }) => oa_id === id) // eslint-disable-line
    state.oas[index] = {
      ...oa,
      ...state.oa
    }
  },
  updateOaForm(sub, { key, value }) {
    state.oa[key] = value
  },
  setOaPagination(sub, { limit, newOffset }) {
    state.pagination = {
      limit,
      offset: newOffset
    }
  },
  stopOaLazzyLoad() {
    state.isMoreOasAvialiable = !state.isMoreOasAvialiable
  },
  removeOa(sub, id) {
    state.oas = state.oas.filter(({ oa_id }) => oa_id !== id) // eslint-disable-line
  },
  clearOaForm() {
    state.oa = {}
  },
  updateSearchType(sub, { value }) {
    state.searchType = value
  }
}

const actions = {
  async createOa({ commit, rootState, dispatch }) {
    const { oa } = rootState.oas
    try {
      await clientApi('post', 'oa', oa)
      await commit('setNewOa', oa)
      await dispatch('showAlert', 'Oa created', { root: true })
    } catch (err) {
      throw err
    }
  },
  async fetchOa({ commit }, { id }) {
    try {
      const { data: { data } } = await clientApi('get', `oa/${id}`)
      commit('setOa', data)
    } catch (err) {
      throw err
    }
  },
  async updateOa({ commit, dispatch }, { id }) {
    const { type, title, parent_id, image, order, url, publish_at } = state.oa
    const oa = {
      type,
      title,
      parent_id,
      image,
      order,
      url,
      publish_at
    }
    try {
      await clientApi(
        'put',
        `oa/${id}`,
        { ...oa }
      )
      commit('updateOa', { oa, id })
      dispatch('showAlert', 'Oa updated', { root: true })
    } catch (err) {
      throw err
    }
  },
  async removeOa({ commit, dispatch }, { id }) {
    try {
      await clientApi('delete', `oa/${id}`)
      await commit('removeOa', id)
      dispatch('showAlert', 'Oa removed', { root: true })
    } catch (err) {
      throw err
    }
  },
  showMoreOas({ commit, rootState, dispatch }) {
    const { pagination: { offset, limit } } = rootState.oas
    const newOffset = offset + limit
    commit('setOasPagination', { limit, newOffset })
    dispatch('fetchOas')
  },
  async fetchOas({ commit, rootState }) {
    const { pagination: { offset, limit } } = rootState.oas
    try {
      const {
        data: {
          oas: { rows, count }
        }
      } = await clientApi('get', `oa?limit=${limit}&offset=${offset}`)
      if (rows.length < count) {
        commit('stopLazzyLoad')
      }
      const oas = rows.map(item => ({
        ...item,
        createdAt: moment(item.createdAt).format('YYYY-MM-DD')
      }))
      commit('setOas', { oas, count })
    } catch (err) {
      throw err
    }
  },

  async globalSearchOa({ commit, dispatch }, s) {
    if (s.length === 0) {
      return dispatch('fetchOas')
    }
    try {
      const { data } = await clientApi(
        'get',
        `oa/search?type=${state.searchType}&s=${s}`
      )
      await commit('setFoundedOas', data)
    } catch (err) {
      throw err
    }
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



