import moment from 'moment'
import { clientApi } from '../../helpers/newClientApi'

const state = {
  locations: [],
  isMoreLocationAvialiable: true,
  pagination: {
    limit: 10,
    offset: 0,
    count: 0
  },
  searchResult: [],
  searchValue: '',
  location: {}
}

const getters = {
  getlocations: () => state.locations,
  isMoreLocationAvialiable: () => state.isMoreLocationAvialiable,
  searchResult: () => state.searchResult
}

const mutations = {
  setLocation(sub, { location }) {
    state.location = {
      ...location,
      hours: location.hours ? JSON.parse(location.hours) : [],
      category: location.category_name,
      formerLocation: {
        lat: location.lat,
        long: location.long
      }
    }
  },
  setLocations(sub, { locations, count }) {
    if (state.pagination.offset > 0) {
      state.locations = [
        ...state.locations,
        ...locations
      ]
    } else {
      state.locations = locations
    }
    state.pagination.count = count
  },
  updateLocation(sub, location, id) {
    const p = state.locations.indexOf(item => item.id === id)
    state.locations[p] = { ...location }
  },
  setLocationPagination(sub, { limit, newOffset }) {
    state.pagination = {
      limit,
      offset: newOffset
    }
  },
  updateLocationForm(sub, payload) {
    state.location[payload.key] = payload.value
  },
  stopLocationLazzyLoad() {
    state.isMoreLocationAvialiable = false
  },
  removeLocation(sub, id) {
    state.locations = state.locations.filter(item => item.location_id !== id)
  },
  setSearchResult(sub, data) {
    state.searchResult = data
  },
  saveSearchValue(sub, s) {
    state.searchValue = s
  },
  addLocation(sub, location) {
    state.location.push(location)
  },
  clearPlaceState(sub) {
    state.location = {}
  }
}

const actions = {
  showAlert({ commit }, msg) {
    const payload = {
      type: 'success',
      msg,
      auto: true
    }
    commit('SET_ALERT', payload)
    commit('DISPLAY_ALERT')
    if (payload.auto) {
      setTimeout(() => {
        commit('DISPLAY_ALERT')
      }, 2000)
    }
  },
  createLocation({ commit, dispatch, rootState: { locations } = [] }) {
    const { location, location: { category_id, hours } } = locations
    const locationCreated = {
      ...location,
      category_id: category_id.category_id,
      category_name: category_id.title,
      hours: JSON.stringify(hours)
    }
    clientApi('post', 'locations', locationCreated)
      .then(() => {
        commit('addLocation', { locationCreated })
        dispatch('showAlert', 'Location created')
      })
      .catch(console.log)
  },
  async updateLocation({ commit, dispatch }) {
    const {
      location,
      location: {
        location_id,
        hours,
        category_id: { category_id, title }
      }
    } = state
    const payload = {
      ...location,
      category_id,
      category_name: title,
      hours: JSON.stringify(hours)
    }
    try {
      await clientApi(
        'put',
        `locations/${location_id}`, // eslint-disable-line
        { ...payload }
      )
      await commit('updateLocation', location, location_id)
      await dispatch('showAlert', 'Category updated')
    } catch (err) {
      throw err
    }
  },
  async removeLocation({ commit, dispatch }, { id }) {
    try {
      await clientApi(
        'delete',
        `locations/${id}`
      )
      commit('removeLocation', id)
      dispatch('showAlert', 'Place deleted')
    } catch (err) {
      throw err
    }
  },
  showMoreLocations({ commit, rootState, dispatch }) {
    const { pagination: { offset, limit } } = rootState.locations
    const newOffset = offset + limit
    commit('setLocationPagination', { limit, newOffset })
    dispatch('fetchLocations')
  },
  fetchLocations({ commit, rootState }) {
    const { pagination: { offset, limit } } = rootState.locations
    clientApi('get', `locations?limit=${limit}&offset=${offset}`)
      .then(({ data: { locations: { rows, count } } }) => {
        if (rootState.locations.locations.length === count) {
          commit('stopLocationLazzyLoad')
        }
        const locations = rows
          .map(item =>
            ({
              ...item,
              _id: item.location_id,
              createdAt: moment(item.createdAt).format('YYYY-MM-DD'),
              updatedAt: moment(item.updatedAt).format('YYYY-MM-DD')
            })
          )
        commit('setLocations', { locations, count })
      })
      .catch(console.log)
  },
  async findLocation({ commit }, s) {
    try {
      const { data } = await clientApi('get', `locations/search?s=${s}`)
      commit('setSearchResult', data)
    } catch (err) {
      throw err
    }
  },
  globalSearch({ commit, dispatch }, s) {
    commit('saveSearchValue', s)
    if (s.length === 0) {
      return dispatch('fetchLocations')
    }
    clientApi('get', `locations/search?s=${s}`)
      .then(({ data }) => {
        const locations = data.map(item =>
          ({
            ...item,
            _id: item.location_id,
            createdAt: moment(item.createdAt).format('YYYY-MM-DD'),
            updatedAt: moment(item.updatedAt).format('YYYY-MM-DD')
          })
        )
        commit('setLocations', { locations, count: 0 })
      })
  },
  async fetchLocation({ commit }, { id }) {
    try {
      const { data: { location } } = await clientApi('get', `locations/${id}`)
      commit('setLocation', { location })
    } catch (err) {
      throw err
    }
  },
  async orderLocations({ dispatch }, items) {
    try {
      items.map(async({ location_id, order }) => {
        await clientApi(
          'put',
          `locations/${location_id}`,
          { order }
        )
      })
      await dispatch(
        'showAlert',
        'Locations was reordered',
        { root: true }
      )
    } catch (err) {
      throw err
    }
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



