import axios from 'axios'

export const clientApi = (method, url, params = {}) =>
  new Promise((resolve, reject) =>
    axios({
      url: `${process.env.API_URL}/${url}`,
      method,
      data: params,
      headers: {
        Authorization: `Bearer ${localStorage.getItem('token')}`
      }
    }).then(resolve)
    .catch(reject)
  )

export const request = async (method, url, params = {}) => {
  try {
    const { data } = await axios[method](`https://localhost:1337/api/v2/${url}`, params)
    return data
  } catch (err) {
    return err
  }
}



