import { clientApi } from '../../helpers/newClientApi'

const state = {
  gallery: []
}

const actions = {
  async uploadImage({ dispatch, commit }, imageObject) {
    try {
      const { data: { gallery } } = await clientApi('post', `gallery`, imageObject)
      await commit('setImage', gallery)
      await dispatch('showAlert', 'Added new image', { root: true })
    } catch (err) {
      throw err
    }
  },
  async removeImage({ dispatch, commit }, key) {
    try {
      await clientApi('delete', `gallery/${key}`)
      await commit('removeImage', key)
      await dispatch('showAlert', 'Image was removed', { root: true })
    } catch (err) {
      throw err
    }
  }
}
const mutations = {
  addExistingImage(sub, images) {
    state.gallery = images
  },
  setImage(sub, image) {
    state.gallery.push(image)
  },
  removeImage(sub, key) {
    state.gallery = state.gallery.filter(
      ({ id }) => id !== key
    )
  }
}
const getters = {
  getGallery: () => state.gallery
}

export default {
  state,
  getters,
  mutations,
  actions
}



