import { clientApi } from '../../helpers/newClientApi'
import {router} from '../../main'

const state = {
  results: [],
  controlForm: {
    objectId: '',
    userId: ''
  }
}

const actions = {
  async submitControlForm({ dispatch, state: { controlForm } }) {
    try {
      await clientApi('post', `permissions`, controlForm)
      await dispatch('showAlert', 'Role was added', { root: true })
      router.go('-1')
    } catch (err) {
      throw err
    }
  },
  async remoteSearch({ commit, state: { controlForm } }, s) {
    const type = controlForm.type === 'event'
      ? 'events/globalSearch'
      : 'locations/search'
    try {
      const { data } = await clientApi('get', `${type}?s=${s}`)
      const results = data
      await commit('setResults', results)
    } catch (err) {
      throw err
    }
  }
}
const mutations = {
  clearRoleSearch() {
    state.results = []
  },
  updateControlForm(sub, { key, value }) {
    state.controlForm[key] = value
  },
  setResults(sub, results) {
    state.results = results
  }
}

const getters = {
  foundResults: () => state.results
}

export default {
  state,
  getters,
  mutations,
  actions
}



