import { clientApi } from '../../helpers/newClientApi'
import { uniq } from 'ramda'

const state = {
  users: [],
  limit: 50,
  error: {},
  userSearchResults: []
}

const getters = {
  getUsers: () => uniq(state.users),
  getUser: () => state.user,
  error: () => state.error,
  userSearchResults: () => state.userSearchResults
}

const mutations = {
  setError(sub, error) {
    state.error = error
  },
  setUser(sub, user) {
    state.userForm = { ...user }
  },
  setUsers(sub, { rows, count }) {
    const repUsers = [ ...state.users, ...rows ]
    state.count = count
    state.users = repUsers
  },
  loadMore() {
    state.limit = state.limit + 30
  },
  addUser(sub, newUser) {
    state.users.push(newUser)
  },
  resetUserForm(sub) {
    for (let key in state.user) {
      state.userForm[key] = ''
    }
  },
  removeUser(sub, id) {
    state.users = state.users.filter(item => item.user_id !== id)
  },
  updateUser(sub, { id, data }) {
    const userIndex = state.users.findIndex(item => item.user_id === id)
    state.users[userIndex] = data
  },
  usersSearchResult(scope, results) {
    state.userSearchResults = results
  }
}
/* eslint-disable */

const actions = {
  loadMore({ dispatch, commit }) {
    commit('loadMore')
    dispatch('fetchUsers')
  },
  async fetchUsers ({ commit }) {
    try {
      const { data: { rows, count } } = await clientApi('get', 'users')
      await commit(
        'setUsers',
        { rows, count }
      )
    } catch (err) {
      return commit('setError', err)
    }
  },
  async userSearch ({ commit }, s) {
    try {
      const { data } = await clientApi('get', `users/search?s=${s}`)
      await commit('usersSearchResult', data)
    } catch (error) {
      return commit('setError', err)
    }
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



