import { parse } from 'query-string'
import { values, pathOr } from 'ramda'
import moment from 'moment'

const parseDays = days =>
  values(parse(`?${days}`, { arrayFormat: 'bracket' }))

const prepareEvent = event => ({
  ...event,
  days: parseDays(event.days),
  category: pathOr('', ['category', 'title'], event),
  everyday: parseDays(event.days).length === 7,
  location: pathOr('', ['location', 'title'], event),
  location_id: pathOr('', ['location', 'location_id'], event),
  dateArray: [
    new Date(Number(event.startDate)),
    new Date(Number(event.endDate))
  ],
  startTime: new Date(event.startTime),
  endTime: new Date(event.endTime),
  startDate: new Date(Number(event.startDate)),
  endDate: new Date(Number(event.endDate)),
  timeData: {
    days: parseDays(event.days),
    dateArray: [
      new Date(Number(event.startDate)),
      new Date(Number(event.endDate))
    ],
    startTime: new Date(event.startTime),
    endTime: new Date(event.endTime),
    afterMidnight: event.afterMidnight,
    eventFrequency: event.eventFrequency,
    startDate: new Date(Number(event.startDate)),
    endDate: new Date(Number(event.endDate)),
    everyday: parseDays(event.days).length === 7
  }
})

const compareDate = date =>
  moment(date).format('YYYY-MM-DD')

const prepareList = items =>
  items.map(item =>
    ({
      ...item,
      ...item.wp_em_location,
      _id: item.event_id,
      createdAt: compareDate(item.createdAt),
      updatedAt: compareDate(item.updatedAt),
      startDate: compareDate(new Date(Number(item.startDate)))
    })
  )

export {
  prepareEvent,
  prepareList
}



