import { clientApi, request } from '../../helpers/newClientApi'
import { stringify } from 'query-string'

const state = {
  highLight: {},
  highLights: [],
  isMoreHighLightsAvialiable: true,
  searchType: 'location',
  pagination: {
    limit: 25,
    count: 0,
    page: 1
  },
  setFoundedItems: []
}

const getters = {
  isMoreHighLightsAvialiable: () => state.isMoreHighLightsAvialiable,
  setFoundedItems: () => state.setFoundedItems,
  getHighLights: () => state.highLights,
  getHighLight: () => state.highLight
}

const mutations = {
  setNewHighLight(sub, highLight) {
    state.highLights = [
      ...state.highLights,
      highLight
    ]
  },
  clearHighLightSearch() {
    state.setFoundedItems = []
  },
  setHighLight(sub, highLight) {
    state.highLight = highLight
  },
  setFoundedHighlights(sub, docs) {
    state.highLights = docs
  },
  setHighLights(sub, { highLights, count, page }) {
    if (page > 1) {
      state.highLights = [
        ...state.highLights,
        ...highLights
      ]
    } else {
      state.highLights = highLights
    }
    state.pagination = {
      ...state.pagination,
      count,
      page
    }
  },
  updateHighLight(sub, { highLight, id }) {
    const index = state.highLights.indexOf(item => item._id === id)
    state.highLights[index] = {
      ...highLight,
      ...state.highLight
    }
  },
  stopHighLightLazzyLoad() {
    state.isMoreHighLightsAvialiable = !state.isMoreHighLightsAvialiable
  },
  updateHighLightForm(sub, { key, value }) {
    state.highLight[key] = value
  },
  removeHighLight(sub, id) {
    state.highLights = state.highLights.filter(item => item._id !== id)
  },
  setFoundedItems(sub, data) {
    state.setFoundedItems = data
  },
  clearHighlightForm() {
    state.highLight = {}
  },
  updateSearchType(sub, { value }) {
    state.searchType = value
  }
}

const actions = {
  async createHighLight({ commit, dispatch }) {
    const { highLight } = state
    try {
      await request('post', 'highlights', highLight)
      await commit('setNewHighLight', highLight)
      await dispatch('showAlert', 'Highlight created', { root: true })
    } catch (err) {
      throw (err)
    }
  },
  async fetchHighLight({ commit }, { id }) {
    try {
      const data = await request('get', `highlights/${id}`)
      await commit('setHighLight', data)
    } catch (err) {
      throw err
    }
  },
  async updateHighLight({ commit, dispatch }, { id }) {
    const { type, title, title_cn, image, text, parent_id } = state.highLight
    const highLight = {
      type,
      title,
      title_cn,
      image,
      text,
      parent_id
    }
    try {
      await request(
        'put',
        `highlights/${id}`,
        { ...highLight }
      )
      await commit('updateHighLight', { highLight, id })
      await dispatch('showAlert', 'HighLight updated', { root: true })
    } catch (err) {
      throw err
    }
  },
  async removeHighLight({ commit, dispatch }, id) {
    try {
      await request('delete', `highlights/${id}`)
      await commit('removeHighLight', id)
      await dispatch('showAlert', 'HighLight removed', { root: true })
    } catch (err) {
      throw err
    }
  },
  showMoreHighLights({ commit, dispatch }) {
    dispatch('fetchHighLights')
  },
  async fetchHighLights({ commit }) {
    const { pagination: { limit, page } } = state
    try {
      const {
        docs, docsCount, nextPage, hasNextPage
      } = await request('get', `highlights?limit=${limit}&page=${page}`)
      if (!hasNextPage) {
        commit('stopHighLightLazzyLoad')
      }
      await commit('setHighLights', { highLights: docs, docsCount, page: nextPage })
    } catch (err) {
      throw err
    }
  },

  async globalSearchHighLight({ commit, dispatch }, s) {
    if (s.length === 0) {
      return dispatch('fetchHighLights')
    }
    const params = stringify({
      type: state.searchType,
      ...s
    })
    try {
      const { docs } = await request(
        'get',
        `highlights/search?${params}`
      )
      await commit('setFoundedHighlights', docs)
    } catch (error) {
      throw error
    }
  },
  async searchParentItem({ commit }, s) {
    const type = state.searchType === 'event' ? 'events/globalSearch' : 'locations/search'
    try {
      const { data } = await clientApi('get', `${type}?s=${s}`)
      await commit('setFoundedItems', data)
    } catch (err) {
      throw err
    }
  },
  async searchTypeItem({ commit }, s) {
    const type = state.highLight.type === 'event' ? 'events/globalSearch' : 'locations/search'
    try {
      const { data } = await clientApi('get', `${type}?s=${s}`)
      await commit('setFoundedItems', data)
    } catch (err) {
      throw err
    }
  },
  async orderHightlights({ dispatch }, items) {
    items.map(async({ highlight_id, order }) => {
      await request(
        'put',
        `highlights/${highlight_id}`,
        { order }
      )
    })
    await dispatch('showAlert', 'HighLights was reordered', { root: true })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



