import Sidebar from './SideBar.vue'

const SidebarStore = {
  showSidebar: false,
  sidebarLinks: [
    {
      name: 'Statistics',
      icon: 'ti-panel',
      path: '/dashboard'
    },
    {
      name: 'Events',
      icon: 'ti-calendar',
      path: '/events/list'
    },
    {
      name: 'Categories',
      icon: 'ti-list',
      path: '/categories/list'
    },
    {
      name: 'HighLights',
      icon: 'ti-light-bulb',
      path: '/highlights/list'
    },
    {
      name: 'Locations',
      icon: 'ti-map',
      path: '/locations/list'
    },
    {
      name: 'Users',
      icon: 'ti-user',
      path: '/users/list'
    },
    {
      name: 'Oas',
      icon: 'ti-text',
      path: '/oas/list'
    },
    {
      name: 'Gallery',
      icon: 'ti-image',
      path: '/galleries/add'
    }
  ],
  displaySidebar(value) {
    this.showSidebar = value
  }
}

const SidebarPlugin = {
  install (Vue) {
    Vue.mixin({
      data () {
        return {
          sidebarStore: SidebarStore
        }
      }
    })
    Reflect.defineProperty(Vue.prototype, '$sidebar', {
      get () {
        return this.$root.sidebarStore
      }
    })
    Vue.component('side-bar', Sidebar)
  }
}

export default SidebarPlugin



