import { clientApi } from '../../helpers/newClientApi'
import { router } from '../../main'

const state = {
  loginForm: {
    email: '',
    password: ''
  },
  tokenError: ''
}

const getters = {
  loginError: () => state.tokenError.message
}

const mutations = {
  updateLoginForm(_, { key, value }) {
    state.loginForm[key] = value
  },
  setToken(_, token) {
    localStorage.setItem('token', token)
    router.replace('/dashboard')
  },
  setTokenError(_) {
    state.tokenError = {
      message: 'These credentials do not match our records.'
    }
  }
}

const actions = {
  signOut() {
    localStorage.removeItem('token')
    router.replace('/')
  },
  async signIn({ commit }) {
    try {
      const { data: { token } } = await clientApi('post', 'users/signin', {
        ...state.loginForm
      })
      await commit('setToken', token)
    } catch (err) {
      await commit('setTokenError')
    }
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



