const routes = [
  {
    path: '/',
    component: () => import('../components/Dashboard/Layout/DashboardLayout.vue'),
    redirect: '/dashboard',
    meta: { mustAuth: true },
    children: [
      {
        path: '/dashboard',
        name: 'Statistics',
        component: () => import('src/components/Dashboard/Views/DashboardContent.vue')
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../components/Dashboard/Layout/Login.vue')
  },
  {
    path: '/galleries',
    component: () => import('../components/Dashboard/Layout/DashboardLayout.vue'),
    redirect: '/galleries',
    meta: { mustAuth: true },
    children: [
      {
        path: 'add',
        name: 'Gallery',
        component: () => import('src/pages/Galleries/GalleryForm.vue')
      }
    ]
  },
  {
    path: '/events',
    component: () => import('../components/Dashboard/Layout/DashboardLayout.vue'),
    redirect: '/events',
    meta: { mustAuth: true },
    children: [
      {
        path: 'list',
        name: 'Events',
        component: () => import('src/pages/Events/EventsList.vue')
      },
      {
        path: 'add',
        name: 'add',
        component: () => import('src/pages/Events/EventsForm.vue')
      },
      {
        path: 'update/:id',
        name: 'update',
        component: () => import('src/pages/Events/EventsForm.vue')
      }
    ]
  },
  {
    path: '/categories',
    component: () => import('../components/Dashboard/Layout/DashboardLayout.vue'),
    redirect: '/categories',
    meta: { mustAuth: true },
    children: [
      {
        path: 'list',
        name: 'Categories',
        component: () => import('src/pages/Categories/CategoriesList.vue')
      },
      {
        path: 'add',
        name: 'add',
        component: () => import('src/pages/Categories/CategoriesForm.vue')
      },
      {
        path: 'update/:id',
        name: 'update',
        component: () => import('src/pages/Categories/CategoriesForm.vue')
      }
    ]
  },
  {
    path: '/users',
    component: () => import('../components/Dashboard/Layout/DashboardLayout.vue'),
    redirect: '/users',
    meta: { mustAuth: true },
    children: [
      {
        path: 'list',
        name: 'users',
        component: () => import('src/pages/Users/UsersList.vue')
      },
      {
        path: 'role/add',
        name: 'role',
        component: () => import('src/pages/Users/PermissionForm.vue')
      }
    ]
  },
  {
    path: '/highlights',
    component: () => import('../components/Dashboard/Layout/DashboardLayout.vue'),
    redirect: '/highlights',
    meta: { mustAuth: true },
    children: [
      {
        path: 'list',
        name: 'HighLights',
        component: () => import('src/pages/HighLights/HighLightsList.vue')
      },
      {
        path: 'add',
        name: 'add',
        component: () => import('src/pages/HighLights/HighLightsForm.vue')
      },
      {
        path: 'update/:id',
        name: 'update',
        component: () => import('src/pages/HighLights/HighLightsForm.vue')
      }
    ]
  },
  {
    path: '/oas',
    component: () => import('../components/Dashboard/Layout/DashboardLayout.vue'),
    redirect: '/oas',
    meta: { mustAuth: true },
    children: [
      {
        path: 'list',
        name: 'Oas',
        component: () => import('src/pages/Oas/OasList.vue')
      },
      {
        path: 'add',
        name: 'add',
        component: () => import('src/pages/Oas/OasForm.vue')
      },
      {
        path: 'update/:id',
        name: 'update',
        component: () => import('src/pages/Oas/OasForm.vue')
      }
    ]
  },
  {
    path: '/locations',
    component: () => import('../components/Dashboard/Layout/DashboardLayout.vue'),
    redirect: '/locations',
    meta: { mustAuth: true },
    children: [
      {
        path: 'list',
        name: 'Locations',
        component: () => import('src/pages/Locations/LocationsList.vue')
      },
      {
        path: 'add',
        name: 'add',
        component: () => import('src/pages/Locations/LocationForm.vue')
      },
      {
        path: 'update/:id',
        name: 'update',
        component: () => import('src/pages/Locations/LocationForm.vue')
      }
    ]
  },
  {
    path: '*',
    component: () => import('../components/GeneralViews/NotFoundPage.vue')
  }
]

export default routes



